import { createStore } from 'vuex'
import { initializeApp } from "firebase/app";
import { collection, getFirestore, getDocs,query, where, onSnapshot, doc, setDoc,addDoc,deleteDoc,updateDoc, update, getAuth } from "firebase/firestore";
import { getStorage, ref, uploadBytesResumable,getDownloadURL } from "firebase/storage";

export default createStore({
  state: {
    config:  {
      apiKey: "AIzaSyBoiXAZJ9STa2-pek7Z6a45UsNUSf3rivM",
      authDomain: "funny-4a96e.firebaseapp.com",
      projectId: "funny-4a96e",
      storageBucket: "gs://funny-4a96e.appspot.com/",
      messagingSenderId: "480722542315",
      appId: "1:480722542315:web:692e9349ee9e6ad38328ae"
    },
    requestData:[],
    request: '',
    owner: '',
    datanotEmpty: false,
    file: '',
    statusUpload: '',
    type: ''
  },
  getters: {
    upStatus (state) {
      return state.statusUpload
    },
    fileget (state) {
      return state.file
    },
    fireConfig (state) {
      return state.config
    },
    getrequestData (state) {
      return state.requestData
    },
    ownerGetter (state) {
      return state.owner
    },
    emPT (state) {
      return state.datanotEmpty
    }
  },
  mutations: {
    configData(state) {
   
      const config = initializeApp(state.config);
      const db = getFirestore(config);

      const q = query(collection(db, "request"));
      const unsubscribe = onSnapshot(q, (querySnapshot) => {
        const temp = [];
        console.log("Current temp in CA: ", querySnapshot);
        querySnapshot.forEach((doc) => {
            temp.push(doc.data());   
        });
        console.log("Current temp in CA: ", temp);
        state.requestData = temp

        for (let x = 0; x < state.requestData.length; x++) {
          const element = state.requestData[x];
          if(element.removed == false){
            state.datanotEmpty = true
            break
          }else{
            state.datanotEmpty = false
          }

          
        }
});
    },
    addData(state, payload){
      const config = initializeApp(state.config);
      const db = getFirestore(config);
      const cityRef =  query(collection(db, "request"));
      addDoc(cityRef, { 
        request_title: payload.data,
        approved_by: '',
        created_by: state.owner,
        removed: false,
        request_status : false,
        file: state.file,
        type: state.type,
        uuid:  Math.floor(Math.random() * (99999 - 10000)) + 10000
       });

       state.file = ''
       state.statusUpload = ''
    },
    deleteRequest(state, payload){
      const config = initializeApp(state.config);
      const db = getFirestore(config);
      const cityRef = query(collection(db, "request"), where("uuid", "==", payload.data));

      const unsubscribe = onSnapshot(cityRef, (querySnapshot) => {
        querySnapshot.forEach((docs) => {
         updateDoc(doc(db, "request", docs.id), {
          removed: true
        });
      
        
      });
      });

    },
    setOwner(state, payload){
      state.owner = payload.data
    },
    uploadFile(state, payload){
     const app  = initializeApp(state.config);
    console.log(state.config)
    const storage = getStorage(app);
    const storageRef = ref(storage, state.owner +'/'+ payload.filename);
    const uploadTask = uploadBytesResumable(storageRef, payload.file);
    console.log('Payload-----------------',payload.file)
    
    uploadTask.on('state_changed', 
  (snapshot) => {
    // Observe state change events such as progress, pause, and resume
    // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
    const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
    console.log('Upload is ' + progress + '% done');

    state.statusUpload = 'Upload is ' + progress + '% done'
    switch (snapshot.state) {
      case 'paused':
        console.log('Upload is paused');
        break;
      case 'running':
        console.log('Upload is running');
        break;
    }
  }, 
  (error) => {
    // Handle unsuccessful uploads
  }, 
  () => {
    // Handle successful uploads on complete
    // For instance, get the download URL: https://firebasestorage.googleapis.com/...
    getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
      console.log('File available at', downloadURL);
      state.file = downloadURL
      state.type = payload.file.type
    });
  }
);

    }
  },
  actions: {
    updateRequest (context) {
      context.commit('configData')
    },
    addRequest ({ commit }, payload) {
        commit('addData', payload)
    },
    deleteReq ({ commit }, payload) {
      commit('deleteRequest', payload)
  },
  setOwnerAcc ({commit}, payload){
    commit('setOwner', payload)
  },

  uploadTrigger ({commit}, payload){
    commit('uploadFile', payload)
  },
  },
  modules: {
  }
})
