<script setup>
import { ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { useStore } from "vuex";
const store = useStore();
const router = useRouter()

const isOpen = ref(false)
const islogged = ref(false)
let monster = ref(0)
let triggerLogged = ref(0)
import { getAuth, signInWithEmailAndPassword, onAuthStateChanged, signOut } from 'firebase/auth'

const allAuth = ref('https://flowbite.com/docs/images/people/profile-picture-5.jpg')
const userImage = ref('')
const dropped = ref(false)

onMounted(() => {
    console.log('allAuth: ', allAuth.value)

    let auth = getAuth()
    monster = setInterval(() => {

        if (auth.currentUser != null) {
            clearInterval(monster)
            // console.log('getAuth: ', auth.currentUser)
            allAuth.value = auth.currentUser
            fetch(auth.currentUser.photoURL, { mode: 'no-cors' }).then((response) => {
                // console.log(response)
         
                    userImage.value = auth.currentUser.photoURL
              

            });
            onAuthStateChanged(auth, (user) => {
                if (user) {
                    islogged.value = true
                     console.log('-----------------------',getAuth().currentUser)
                     let ownerName = ''
                     if(getAuth().currentUser.displayName != null){
                        ownerName = getAuth().currentUser.displayName
                     }else{
                        ownerName = getAuth().currentUser.email
                     }
                        store.commit("setOwner", {
                            data: ownerName,
                        });
                } else {
                    islogged.value = false
                    router.push('/login')
                }
            })

        } else {
            console.log('loading: ', monster)
            triggerLogged.value++
            if (triggerLogged.value == 10) {
                checkAuth()
                clearInterval(monster)
            }

        }

    }, 100);


})




const checkAuth = () => {
    let auth = getAuth()

    signOut(auth).then(() => {
        router.push('/login')
    })
}
</script>

<template >
    <div>
        <nav x-data="{ isOpen: false }" class="relative bg-white shadow dark:bg-gray-800">
            <div class="container px-6 py-4 mx-auto md:flex md:justify-between md:items-center">
                <div class="flex items-center justify-between">
                    <a href="#">
                        <img class="w-auto h-12 max-w-xs" src="../assets/logo2.png" alt="">
                    </a>

                    <!-- Mobile menu button -->
                    <div class="flex lg:hidden">
                        <button x-cloak @click="isOpen = !isOpen" type="button"
                            class="text-gray-500 dark:text-gray-200 hover:text-gray-600 dark:hover:text-gray-400 focus:outline-none focus:text-gray-600 dark:focus:text-gray-400"
                            aria-label="toggle menu">
                            <svg x-show="!isOpen" xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none"
                                viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                                <path stroke-linecap="round" stroke-linejoin="round" d="M4 8h16M4 16h16" />
                            </svg>

                            <svg x-show="isOpen" xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none"
                                viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                                <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
                            </svg>
                        </button>
                    </div>
                </div>

                <!-- Mobile Menu open: "block", Menu closed: "hidden" -->
                <div class="flex item-center cursor-pointer">
                    <!-- <p class="font-bold text-gray-700 mr-2 my-auto">{{ allAuth.displayName }}</p> -->
                    <img v-if="userImage" @click="dropped = ! dropped" class="w-10 h-10 rounded-full" :src="userImage" alt="Rounded avatar">
                    <div v-if="!userImage"  @click="dropped = ! dropped"  class="relative inline-flex items-center justify-center w-10 h-10 overflow-hidden bg-gray-100 rounded-full dark:bg-gray-600">
                        <span class="font-medium text-gray-600 ">USR</span>
                    </div>
                    <!-- <div class="flex flex-col md:flex-row md:mx-6">
                        <button @click="checkAuth()" v-if="islogged"
                            class="my-2 text-gray-700 transition-colors duration-300 transform dark:text-gray-200 hover:text-blue-500 dark:hover:text-blue-400 md:mx-4 md:my-0">Sign
                            Out</button>
                    </div> -->
                </div>
                <div v-if="dropped" id="dropdownInformation"
                    class="z-10  fixed right-[10%] top-[9%] overflow-hidden bg-white divide-y divide-gray-100 rounded-lg shadow w-44 ">
                    <div class="px-4 py-3 text-sm text-gray-900 ">
                        <div v-if="allAuth.displayName">{{allAuth.displayName}}</div>
                        <p class="break-words" v-if="!allAuth.displayName">UUID: <strong>{{ allAuth.uid }}</strong></p>
                        <p class="font-medium break-words">{{allAuth.email}}</p>
                    </div>
                 
                    <div class="py-2">
                        <a @click="checkAuth()"
                            class="block px-4 py-2 cursor-pointer text-sm text-gray-700 hover:bg-gray-100  text-red-600">Sign
                            out</a>
                    </div>
                </div>



                <!-- <div x-cloak :class="[isOpen ? 'translate-x-0 opacity-100 ' : 'opacity-0 -translate-x-full']" class="absolute inset-x-0 z-20 w-full px-6 py-4 transition-all duration-300 ease-in-out bg-white dark:bg-gray-800 md:mt-0 md:p-0 md:top-0 md:relative md:bg-transparent md:w-auto md:opacity-100 md:translate-x-0 md:flex md:items-center">
            <div class="flex flex-col md:flex-row md:mx-6">
                <button @click="checkAuth()"  v-if="islogged" class="my-2 text-gray-700 transition-colors duration-300 transform dark:text-gray-200 hover:text-blue-500 dark:hover:text-blue-400 md:mx-4 md:my-0" >Sign Out</button>
            </div>

           
        </div> -->
        </div>
    </nav>
</div></template>
