const email = ref('')
const password = ref('')
const msg = ref('')

const login = () =>{
    const auth = getAuth()
    signInWithEmailAndPassword(auth, email.value, password.value).then((data)=>{
        msg.value = 'success!'
        setTimeout(() => {
            router.push('/home')
        }, 3000);
        
    }).catch((error) => {
        console.log(error.code)
        msg.value = error.code
    })
}

